/* Component Classes - Using modern CSS features */

/* Buttons */
.btn {
  --btn-padding-x: var(--space-4);
  --btn-padding-y: var(--space-2);
  --btn-font-size: var(--text-base);
  --btn-font-weight: 500;
  --btn-radius: var(--radius);
  --btn-transition: var(--transition);
  
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-2);
  padding: var(--btn-padding-y) var(--btn-padding-x);
  font-size: var(--btn-font-size);
  font-weight: var(--btn-font-weight);
  line-height: 1.5;
  border-radius: var(--btn-radius);
  border: 1px solid transparent;
  cursor: pointer;
  transition: var(--btn-transition);
  white-space: nowrap;
  user-select: none;
  text-decoration: none;
  position: relative;
  isolation: isolate;
}

.btn:hover {
  transform: translateY(-1px);
}

.btn:active {
  transform: translateY(0);
}

.btn:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Button variants using data attributes */
.btn[data-variant="primary"] {
  background: var(--primary);
  color: white;
  border-color: var(--primary);
}

.btn[data-variant="primary"]:hover {
  background: var(--primary-dark);
  border-color: var(--primary-dark);
  box-shadow: var(--shadow-md);
}

.btn[data-variant="secondary"] {
  background: var(--secondary);
  color: white;
  border-color: var(--secondary);
}

.btn[data-variant="outline"] {
  background: transparent;
  color: var(--primary);
  border-color: var(--primary);
}

.btn[data-variant="outline"]:hover {
  background: var(--primary);
  color: white;
}

.btn[data-variant="ghost"] {
  background: transparent;
  color: var(--text);
  border-color: transparent;
}

.btn[data-variant="ghost"]:hover {
  background: var(--bg-secondary);
  color: var(--primary);
}

.btn[data-variant="danger"] {
  background: var(--danger);
  color: white;
  border-color: var(--danger);
}

/* Button sizes */
.btn[data-size="sm"] {
  --btn-padding-x: var(--space-3);
  --btn-padding-y: var(--space-1);
  --btn-font-size: var(--text-sm);
}

.btn[data-size="lg"] {
  --btn-padding-x: var(--space-6);
  --btn-padding-y: var(--space-3);
  --btn-font-size: var(--text-lg);
}

/* Cards */
.card {
  background: var(--bg);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  overflow: hidden;
  transition: var(--transition);
}

.card:hover {
  box-shadow: var(--shadow-lg);
}

.card-header {
  padding: var(--space-4);
  border-bottom: 1px solid var(--border);
}

.card-body {
  padding: var(--space-4);
}

.card-footer {
  padding: var(--space-4);
  border-top: 1px solid var(--border);
  background: var(--bg-secondary);
}

/* Forms */
.form-group {
  margin-bottom: var(--space-4);
}

.form-label {
  display: block;
  margin-bottom: var(--space-2);
  font-weight: 500;
  font-size: var(--text-sm);
  color: var(--text);
}

.form-control {
  width: 100%;
  padding: var(--space-2) var(--space-3);
  font-size: var(--text-base);
  line-height: 1.5;
  color: var(--text);
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  transition: var(--transition);
  appearance: none;
}

.form-control:hover {
  border-color: var(--text-light);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px var(--primary-alpha);
}

.form-control:disabled {
  background: var(--bg-tertiary);
  cursor: not-allowed;
  opacity: 0.6;
}

.form-control::placeholder {
  color: var(--text-lighter);
}

/* Input variants */
.form-control[data-state="error"] {
  border-color: var(--danger);
}

.form-control[data-state="error"]:focus {
  box-shadow: 0 0 0 3px hsla(0, 100%, 50%, 0.1);
}

.form-control[data-state="success"] {
  border-color: var(--success);
}

/* Checkbox and Radio - Modern custom styling */
.form-check {
  display: flex;
  align-items: center;
  gap: var(--space-2);
  margin-bottom: var(--space-2);
}

.form-check-input {
  -webkit-appearance: none;
  appearance: none;
  width: 1.25rem;
  height: 1.25rem;
  border: 2px solid var(--border);
  border-radius: var(--radius-sm);
  transition: var(--transition);
  cursor: pointer;
  flex-shrink: 0;
  display: grid;
  place-content: center;
}

.form-check-input[type="radio"] {
  border-radius: var(--radius-full);
}

.form-check-input:hover {
  border-color: var(--primary);
}

.form-check-input:checked {
  background: var(--primary);
  border-color: var(--primary);
}

.form-check-input:checked::before {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  background: white;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}

.form-check-input[type="radio"]:checked::before {
  clip-path: circle(50%);
}

.form-check-input:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

.form-check-label {
  cursor: pointer;
  user-select: none;
}

/* Alerts */
.alert {
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius);
  border: 1px solid transparent;
  display: flex;
  align-items: flex-start;
  gap: var(--space-3);
  margin-bottom: var(--space-4);
  position: relative;
}

.alert[data-variant="success"] {
  background: hsla(158, 64%, 52%, 0.1);
  border-color: var(--success);
  color: hsl(158, 64%, 30%);
}

.alert[data-variant="danger"] {
  background: hsla(0, 84%, 60%, 0.1);
  border-color: var(--danger);
  color: hsl(0, 84%, 40%);
}

.alert[data-variant="warning"] {
  background: hsla(35, 92%, 33%, 0.1);
  border-color: var(--warning);
  color: hsl(35, 92%, 25%);
}

.alert[data-variant="info"] {
  background: hsla(217, 91%, 60%, 0.1);
  border-color: var(--info);
  color: hsl(217, 91%, 40%);
}

/* Badge */
.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-1) var(--space-2);
  font-size: var(--text-xs);
  font-weight: 600;
  line-height: 1;
  border-radius: var(--radius-full);
  white-space: nowrap;
}

.badge[data-variant="primary"] {
  background: var(--primary);
  color: white;
}

.badge[data-variant="secondary"] {
  background: var(--secondary);
  color: white;
}

.badge[data-variant="success"] {
  background: var(--success);
  color: white;
}

.badge[data-variant="danger"] {
  background: var(--danger);
  color: white;
}

.badge[data-variant="warning"] {
  background: var(--warning);
  color: white;
}

/* Modal */
.modal {
  position: fixed;
  inset: 0;
  z-index: var(--z-modal);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-4);
  opacity: 0;
  visibility: hidden;
  transition: var(--transition);
}

.modal[data-state="open"] {
  opacity: 1;
  visibility: visible;
}

.modal-backdrop {
  position: absolute;
  inset: 0;
  background: rgb(0 0 0 / 0.5);
  z-index: var(--z-modal-backdrop);
}

.modal-content {
  position: relative;
  background: var(--bg);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-xl);
  max-width: 500px;
  width: 100%;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  z-index: var(--z-modal);
  transform: scale(0.9) translateY(20px);
  transition: transform var(--transition);
}

.modal[data-state="open"] .modal-content {
  transform: scale(1) translateY(0);
}

/* Loading states */
.spinner {
  --spinner-size: 1.25rem;
  --spinner-border: 2px;
  --spinner-speed: 0.9s;
  
  display: inline-block;
  width: var(--spinner-size);
  height: var(--spinner-size);
  border: var(--spinner-border) solid var(--border);
  border-radius: 50%;
  border-top-color: var(--primary);
  animation: spin var(--spinner-speed) linear infinite;
}

.spinner[data-size="sm"] {
  --spinner-size: 1rem;
}

.spinner[data-size="lg"] {
  --spinner-size: 2rem;
  --spinner-border: 3px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Skeleton loading */
.skeleton {
  background: linear-gradient(
    90deg,
    var(--bg-tertiary) 25%,
    var(--bg-secondary) 50%,
    var(--bg-tertiary) 75%
  );
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s ease-in-out infinite;
  border-radius: var(--radius-sm);
}

@keyframes skeleton-loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Progress bar */
.progress {
  height: 0.5rem;
  background: var(--bg-tertiary);
  border-radius: var(--radius-full);
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background: var(--primary);
  border-radius: var(--radius-full);
  transition: width var(--transition);
}

/* Tabs */
.tabs {
  display: flex;
  border-bottom: 1px solid var(--border);
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}

.tabs::-webkit-scrollbar {
  display: none;
}

.tab {
  padding: var(--space-3) var(--space-4);
  color: var(--text-light);
  font-weight: 500;
  white-space: nowrap;
  cursor: pointer;
  transition: var(--transition);
  position: relative;
  border-bottom: 2px solid transparent;
  margin-bottom: -1px;
}

.tab:hover {
  color: var(--primary);
}

.tab[data-state="active"] {
  color: var(--primary);
  border-bottom-color: var(--primary);
}

/* Dropdown */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 200px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  box-shadow: var(--shadow-lg);
  padding: var(--space-2);
  margin-top: var(--space-1);
  z-index: var(--z-dropdown);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: var(--transition);
}

.dropdown[data-state="open"] .dropdown-menu {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.dropdown-item {
  display: block;
  padding: var(--space-2) var(--space-3);
  color: var(--text);
  border-radius: var(--radius-sm);
  transition: var(--transition);
  cursor: pointer;
}

.dropdown-item:hover {
  background: var(--bg-secondary);
  color: var(--primary);
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

@keyframes slideUp {
  from {
    transform: translateY(10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideDown {
  from {
    transform: translateY(-10px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.animate-fadeIn { animation: fadeIn var(--transition); }
.animate-fadeOut { animation: fadeOut var(--transition); }
.animate-slideUp { animation: slideUp var(--transition); }
.animate-slideDown { animation: slideDown var(--transition); }